@import "../../scss/_mixin.scss";
.container {
  height: 100vh;

  .content {
    height: 100%;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;

    .title {
      font-size: 8rem;

      @include image-text(linear-gradient(120deg, #4053be 50%, #bbb 50%));

      background-size: 220%;
      background-position: 100%;
      transition: all 0.5s;

      &.active {
        background-position: 0%;
      }
    }

    z-index: 2;
  }
  .end {
    position: relative;
    z-index: -1;
    height: 30%;
    transform: translateY(-100%);
  }
}
