@mixin image-text($image) {
  background-image: $image;
  color: transparent;
  background-clip: text;
}

@mixin center($top: 50%, $left: 50%) {
  position: absolute;
  top: $top;
  left: $left;
  transform: translate(-50%, -50%);
}

@mixin fontTranslate($x, $y, $size: 0.85) {
  font-size: #{$size}rem;
  transform: translate(#{$x}rem, #{$y}rem);
}

@mixin mw($breakpoint) {
  @if $breakpoint == l {
    @media only screen and (max-width: $bp-l) {
      @content;
    }
  }

  @if $breakpoint == m {
    @media only screen and (max-width: $bp-m) {
      @content;
    }
  }

  @if $breakpoint == s {
    @media only screen and (max-width: $bp-s) {
      @content;
    }
  }

  @if $breakpoint == xs {
    @media only screen and (max-width: $bp-xs) {
      @content;
    }
  }
}
